import React, { useState } from 'react'
import ImgCrop from 'antd-img-crop'
import { Form, Upload, Icon } from 'antd'
import { useTranslation } from 'react-i18next'
import './index.styl'

const FormItem = Form.Item

const PhotoSection = () => {
  const [base64Url, setBase64Url] = useState(null)

  const { t } = useTranslation()

  const getBase64Url = file =>
    new Promise((resolve, reject) => {
      const reader = new window.FileReader()
      reader.readAsDataURL(file)
      reader.onload = () => {
        setBase64Url(reader.result)
        resolve()
      }
      reader.onerror = error => reject(error)
    })

  const beforeUpload = (file, fileList) => {
    getBase64Url(file)
    return false
  }

  return (
    <div className='PhotoSection'>
      <FormItem label={t('photoStepDescr')}>
        <ImgCrop width={200} height={200}>
          <Upload
            action='/cv-generator'
            listType='picture-card'
            fileList={[]}
            beforeUpload={beforeUpload}
          >
            <Icon type='plus' />
          </Upload>
        </ImgCrop>
      </FormItem>
      {
        base64Url &&
          <div
            className='PhotoSection-preview'
            style={{ backgroundImage: `url(${base64Url})` }}
          />
      }
    </div>
  )
}

export default PhotoSection
