const FormItem = Form.Item
const TextArea = Input.TextArea

const MainInfoSection = () => {
  const { t } = useTranslation()

  return (
    <div className='MainInfoSection'>
      <FormItem label={t('name')}>
        <Input placeholder={t('namePlchldr')} />
      </FormItem>
      <FormItem label={t('jobTitle')}>
        <AutoComplete placeholder={t('jobTitlePlchldr')} />
      </FormItem>
      <FormItem label={t('professionalSummary')}>
        <TextArea placeholder={t('professionalSummaryPlchlder')} />
      </FormItem>
    </div>
  )
}

export default MainInfoSection
